@import '../../settings/lib/ro-shade';
@import 'microscope/color';
@import 'microscope/grid';
@import 'microscope/typography';
@import 'constants';


@mixin checkbox {
  display: inline-flex;
  align-items: center;
  position: relative;
  box-sizing: border-box;

  .input {
    @include overlay;
    margin: 0;
    opacity: 0;
    z-index: 20;
  }

  &__checkmark {
    @include color-background;
    @include border(all, shade(7), 2px);
    width: $checkbox-size;
    height: $checkbox-size;
    background-size: contain;
  }

  .input:checked + &__checkmark {
    background-image: url('data:image/png;base64,');
  }

  .input:focus + &__checkmark {
    @include shade-color(heaven-blue, 1, outline-color, $force: true);
    outline: 2px solid;
  }

  .label {
    font-weight: normal;
    margin-left: $checkbox-spacing;
  }
}

@mixin input {
  @include border(all);
  @include color-background;
  appearance: none;
  border-radius: 0;
  display: inline-flex;
  align-items: center;
  outline: none;
  padding: 0 $input-padding-h;
  position: relative;

  &:disabled {
    opacity: 0.2;
  }

  &:not([type=checkbox]):not([type=radio]) {
    @include body;
    border: none;
  }

  &#{&}--primary {
    @include color-primary(border-color);
    @include color-primary(color);
  }

  &::placeholder {
    color: $input-color-placeholder;
  }

  &:focus {
    @include shade-color(heaven-blue, 1, outline-color, $force: true);
    outline: 2px solid;
  }

  &--error {
    @include shade-color(red, $property: outline-color, $force: true);
    outline: 2px solid;
  }

  &[readonly]:focus,
  &--readonly:focus {
    outline-color: $color-gray!important;
  }
}
