@import 'microscope/color';
@import 'microscope/grid';
@import 'microscope/transform';
@import 'microscope/typography';
@import 'constants';
@import 'mixins';


.button {
  @include body;
  @include rows(1);
  @include color-background;
  @include color-primary(color);
  @include ellipsis;
  appearance: none;
  border: 1px solid transparent;
  box-sizing: border-box;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: $button-padding;
  text-decoration: none;

  &[disabled] {
    opacity: 0.2;
    border-style: dashed;
  }

  &--active,
  &--primary,
  &--hover:hover {
    @include color-background(color);
    @include color-primary(background-color);
  }

  &--open {
    @include color-primary(border-color);
  }

  @each $color-name, $color in $colors {
    &--#{$color-name} {
      @include color-background(color);
      background-color: $color;

      &:hover {
        @include color-background(background-color);
        border-color: $color;
        color: $color;
      }
    }

    &--open#{&}--#{$color-name} {
      &:hover {
        @include color-background(color);
        background-color: $color;
      }
    }
  }

  &--no-pad,
  &--no-text {
    padding-left: 0;
    padding-right: 0;
  }

  &--small {
    @include button--small;
  }

  &--danger {
    @include color-background(color);
    background-color: $button-color-danger;
    border-color: $button-color-danger;

    .icon:before {
      @include color-background(background-color);
    }
  }

  &--danger#{&}--hover:hover {
    @include color-background(background-color, true);
    color: $button-color-danger;
    border-color: $button-color-danger;

    .icon:before {
      background-color: $button-color-danger;
    }
  }

  &--info {
    @include color-background(color);
    background-color: $button-color-info;
    border-color: $button-color-info;

    .icon:before {
      @include color-background(background-color);
    }
  }

  &--info#{&}--hover:hover {
    @include color-background(background-color, true);
    color: $button-color-info;
    border-color: $button-color-info;

    .icon:before {
      background-color: $button-color-info;
    }
  }

  &--success {
    @include color-background(color);
    background-color: $button-color-success;
    border-color: $button-color-success;

    .icon:before {
      @include color-background(background-color);
    }
  }

  &--success#{&}--hover:hover {
    @include color-background(background-color, true);
    color: $button-color-success;
    border-color: $button-color-success;

    .icon:before {
      background-color: $button-color-success;
    }
  }

  &--rounded {
    border-radius: $button-height-small / 2;
  }

  .icon {
    cursor: pointer;
    display: flex;
    align-items: center;
    flex-shrink: 0;
  }

  &__label + .icon {
    margin-left: $button-spacing;
  }

  &--no-text .icon {
    margin-left: 0;
    margin-right: 0;
  }

  .icon:before {
    padding: 0 !important;
  }

  &--dashed {
    border-style: dashed;
  }

  &--no-text &__label {
    display: none;
  }

  &--transparent {
    background-color: transparent !important;
    @include color-primary(color);

    .icon:before {
      background-color: transparent !important;
    }
  }

  &--transparent#{&}--dashed {
    @include color-primary(border-color);
  }


  &--transparent#{&}--hover:hover {
    @include color-background(color);
    @include color-primary(background-color, true);


    .icon:before {
      @include color-background(background-color);
    }
  }

  &--transparent#{&}--success {
    color: $button-color-success;
    border-color: $button-color-success;

    .icon:before {
      background-color: $button-color-success;
    }
  }

  &--transparent#{&}--hover#{&}--success:hover {
    @include color-background(color);
    background-color: $button-color-success !important;

    .icon:before {
      @include color-background(background-color);
    }
  }

  &--transparent#{&}--danger {
    @include color-background(background-color, true);
    color: $button-color-danger;
    border-color: $button-color-danger;

    .icon:before {
      background-color: $button-color-danger;
    }
  }

  &--transparent#{&}--hover#{&}--danger:hover {
    @include color-background(color);
    background-color: $button-color-danger !important;
    border-color: $button-color-danger;

    .icon:before {
      @include color-background(background-color);
    }
  }
}
